html{height:100%}body{height:100%;margin:0}#root{color:#4e4e4e;font-family:sans-serif;height:100%}h1,h2,h3,h4,h5{color:rgba(0,0,0,.6);font-family:sans-serif;font-weight:100}h1{font-weight:400}h3,h4,h5{margin-bottom:.25rem}label{box-sizing:border-box;color:rgba(78,78,78,.75);display:block;font-family:sans-serif;font-size:1rem;font-weight:100;margin:.25rem}br{line-height:1.5rem}main{min-height:100vh}main>section{margin-bottom:2em}header>article{background:0 0}article{border-radius:.25rem;box-shadow:0 4px 8px rgba(0,0,0,.1);margin:.25rem;padding:1.25rem 1.5rem;text-align:left}article.padded{padding:.75rem 2.5rem}article.padded>div{margin:.25rem}article.error{background:#fff6f6;border-color:#e0b4b4;color:#9f3a38}article.warning{background:#ffffe0;border-color:#eeeeae}article>ul{margin:0;padding:0}